*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

.quickbook {

    width: 100%;
    height: auto;
    background-color: #f6f6f9;
    padding: 40px 0px 0px 0px;
 
    position: relative;
}
.quickbook h1{
    font-size: 50px;
    color: #003f69;
    margin: 50px 0px 0px 0px;
    font-weight: bold;
    
}
.quickbook p{
    font-size: 22px;
    color: #003f69;
    margin-top: 20px;
    margin-bottom: 20px;
    
   
}
.body-container-subcontent-container h3{

    font-size: 22px;
    color: #003f69;
    margin: 30px 20px 20px 0px;
}
.body-container-subcontent-container p{
    color: #6e90a7;
    font-size: 17px;
   
}

.feature-accordion h3{
    font-size: 22px;
    color: #003f69;
    padding: 20px 0px 20px 0px;
    letter-spacing: 1px;
    border-bottom: 1px solid #f2f2f2;
    text-align: center;
}
.feature-accordion ul{

    font-size: 22px;
   
    list-style: none;
    padding: 0px 0px 0px 120px;
   
   
}
.feature-accordion li{

   
    line-height: 20px;
     
    
    list-style: none;
    
  
     

}
.feature-accordion ul li a{

    position: relative;
    
    color: #003f69;
    text-decoration: none;
    font-size: 20px;
    line-height:70px;

}
.feature-accordion ul ul{
   
    position: static;
    display: block;
    padding: 0px 0px 0px 0px;
}
.feature-accordion .ul .billing-show.show {

    display: block;
}



.feature-accordion .ul ul li{

    line-height: 0px;
     border-bottom:none;
    
}
.feature-accordion-parent{

    border-bottom: 1px  solid  #f2f2f2;
}
.span{

    position:absolute;
    top: -20px;
    margin: 0px 100px 0px 200px;
    cursor: pointer;
    font-size: 25px;
    background:transparent;
    height: 80px;
    display: block;
   outline: 0;
   color: #6e90a7;
   transform: rotate(-180deg);
   -webkit-transform: rotate(-180deg);
   
  
 }
 .span .fa:hover{
    transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    
     

 }

 .feature-accordion ul ul li a {
    line-height: 40px;
     border-bottom: none;
    color: #6e90a7;

 }
 .feature-accordion-menu2-integration{

    background-color: #f1f1f1;
    color: #6e90a7;
    padding: 0px 0px 0px 0px;
 }

